html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
pre,
a,
address,
cite,
em,
img,
q,
s,
small,
strike,
strong,
tt b,
u,
i,
center,
menu,
ol,
ul,
li,
form,
label,
legend,
table,
caption,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
main,
menu,
nav,
ruby,
section,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
    text-decoration: none;
    color: #000;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
    display: block;
}

*[hidden] {
    display: none;
}

html,
body {
    font-size: 10px;
}

body {
    line-height: 1;
}

menu,
ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

@mixin ct {
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin blue_btn {
    box-shadow: 0 0 10px 3px rgba(#00bfff, .5);
    transition: .2s linear;

}


.container {
    width: 100%;
    height: 100%;

    .navbar {
        height: 10rem;
        display: flex;
        align-items: center;
        justify-content: center;
        border-bottom: 1px solid #f0f0f1;
        background-color: #fff;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 100;

        .nav_container {
            width: 1200px;
            display: flex;

            .nav_wrap {
                display: flex;
                align-items: center;

                .nav_link_container {
                    display: flex;
                    margin-left: 4rem;
                    height: 40px;
                    line-height: 40px;

                    li {
                        margin-right: 5.5rem;

                        a {
                            font-size: 1.5rem;
                        }

                        &.link_dropdown {

                            &:hover .link_wrap {
                                display: block;
                            }
                        }

                        .link_wrap {
                            position: absolute;
                            top: 65px;
                            border: 1px solid #f0f0f1;
                            border-radius: 10px;
                            overflow: hidden;
                            display: none;

                            li {
                                margin: 0;
                                border-bottom: 1px solid #e6e6e6;
                                width: 200px;
                                height: 65px;
                                line-height: 65px;

                                &:last-child {
                                    border: none;
                                }

                                &:hover {
                                    background: #e6e6e6;
                                    font-weight: bold;
                                }

                                a {
                                    display: block;
                                    width: 100%;
                                    height: 100%;
                                    padding-left: 2.5rem;

                                    &:hover {
                                        color: #01bfff;
                                    }
                                }
                            }
                        }
                    }
                }
            }

            .nav_main_container {
                display: flex;
                margin-left: auto;

                .nav_main_wrap {
                    position: relative;
                    z-index: 5;
                    width: 170px;
                    height: 60px;
                    display: flex;
                    align-items: center;

                    .nav_main_box {
                        display: flex;
                        align-items: center;
                        cursor: pointer;
                        width: 170px;
                        height: 48px;

                        &.nav_main_bsc {
                            position: absolute;
                            z-index: 2;
                            transition: all 0.2s linear;
                            border: 1px solid #f0f0f1;
                            border-radius: 5px;
                            background-color: #fff;

                            &:hover {
                                border: 1px solid #01bfff;
                            }
                        }

                        img {
                            width: 54px;
                            height: 54px;
                        }

                        h2 {
                            font-size: 1.5rem;
                            font-weight: bold;
                        }

                    }

                    .nav_main_ethereum_wrap {
                        position: absolute;
                        top: 4.5rem;
                        border: 1px solid #f0f0f1;
                        border-top: none;
                        border-radius: 0 0 5px 5px;
                        padding-top: 1rem;
                        display: none;
                        background: #fff;

                        &.show {
                            display: block;
                        }
                    }
                }

                .nav_btn_container {
                    display: flex;
                    align-items: center;
                    margin-left: 2rem;

                    .nav_btn {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 170px;
                        height: 48px;
                        line-height: 48px;
                        border-radius: 48px;
                        border: 1px solid #0099cb;
                        text-align: center;
                        box-shadow: 0 0 0 1px rgba(#00bfff, .5);
                        animation: shadowe 3s linear infinite;

                        @keyframes shadowe {
                            0% {
                                box-shadow: 0 0 0 1px rgba(#00bfff, .5);
                            }

                            50% {
                                box-shadow: 0 0 10px 1px rgba(#00bfff, .5);
                            }

                            100% {
                                box-shadow: 0 0 0 1px rgba(#00bfff, .5);
                            }
                        }

                        p {
                            width: 100%;
                            height: 100%;

                            a {
                                display: block;
                                width: 100%;
                                height: 100%;
                                font-size: 1.5rem;
                                font-weight: bold;
                                color: #0099cb;
                            }
                        }
                    }
                }
            }
        }
    }

    .main {
        margin: 100px auto 0;
        width: 1200px;

        .main_title {
            position: relative;
            padding: 20rem 0;
            display: flex;
            justify-content: space-between;

            .main_title_text_wrap {
                @include ct;
                align-items: inherit;
                flex-direction: column;

                h1 {
                    font-size: 4rem;
                    line-height: 1.5;
                    text-align: left;

                    strong {
                        font-weight: 600;

                        span {
                            color: #00bfff;
                        }
                    }
                }

                p {
                    margin: 3.5rem 0 5.5rem 0;
                    font-size: 1.9rem;
                    line-height: 2;
                }

                .main_title_btn_wrap {
                    display: flex;

                    .main_title_btn {
                        width: 224px;
                        height: 56px;
                        border-radius: 56px;
                        border: 1px solid #00bfff;

                        &:hover {
                            @include blue_btn;
                        }

                        p {
                            margin: 0;

                            a {
                                display: block;
                                text-align: center;
                                width: 100%;
                                height: 100%;
                                line-height: 56px;
                                color: #fff;
                            }
                        }

                        &:first-child {
                            background: #00bfff;
                            margin-right: 3rem;
                        }

                        &:last-child {
                            &:hover a {
                                background-position: 0 100%;
                            }

                            a {
                                position: relative;
                                display: inline-block;
                                color: #000;
                                overflow: hidden;
                                background: linear-gradient(to right, #00bfff, #00bfff 50%, #000 50%);
                                background-clip: text;
                                -webkit-background-clip: text;
                                -webkit-text-fill-color: transparent;
                                background-size: 200% 100%;
                                background-position: 100%;
                                transition: background-position 275ms ease;
                            }
                        }
                    }
                }
            }

            .main_title_img_wrap {
                width: 515px;

                img {
                    position: absolute;
                    z-index: 1;

                    &:first-child {
                        top: 110px;
                        z-index: 30;
                        animation: sky 5s linear infinite;

                        @keyframes sky {
                            0% {
                                top: 110px;
                            }

                            50% {
                                top: 80px;
                            }
                        }
                    }

                    &:last-child {
                        top: 300px;
                    }
                }
            }
        }
    }
}

.main_sec {

    .main_sec_title {
        padding: 20rem 0;
        display: flex;

        .main_sec_text_wrap {
            display: flex;


            .main_sec_number_text {
                h1 {
                    font-size: 4rem;
                    font-weight: bold;
                    color: #00bfff;
                    line-height: 1.4;
                    margin: 0 2.5rem;
                }

            }

            .main_sec_text {

                h1 {
                    font-size: 4rem;
                    line-height: 1.5;
                    text-align: left;

                    strong {
                        font-weight: 600;

                        span {
                            color: #00bfff;
                        }
                    }
                }

                p {
                    margin: 3.5rem 0 5.5rem 0;
                    font-size: 1.9rem;
                    line-height: 2;
                }

                .main_sec_btn_wrap {

                    .main_sec_btn {
                        width: 224px;
                        height: 56px;
                        border-radius: 56px;
                        border: 1px solid #00bfff;

                        &:hover {
                            @include blue_btn;
                        }

                        p {
                            margin: 0;

                            a {
                                display: block;
                                text-align: center;
                                width: 100%;
                                height: 100%;
                                line-height: 56px;
                                color: #fff;
                            }
                        }

                        &:first-child {
                            background: #00bfff;
                            margin-right: 3rem;
                        }
                    }
                }
            }
        }
    }

    .main_sec_img_wrap {
        width: 513px;
        position: relative;

        img {
            position: absolute;
            z-index: 1;

            &.fadein_down {
                width: 513px;
                bottom: 150px;
                left: 0;
                z-index: 10;
                animation: down 10s linear infinite;

                @keyframes down {
                    0% {
                        opacity: 0;
                        bottom: 150px;
                    }

                    25% {
                        opacity: 1;
                        bottom: 100px;
                    }

                    40% {
                        bottom: 150px;
                    }

                    55% {
                        bottom: 100px;
                    }

                    70% {
                        bottom: 150px;
                    }

                    100% {
                        bottom: -200px;
                    }
                }
            }

            &.cover {
                top: 145px;
                z-index: 11;
            }
        }
    }

    .main_sec2_img_wrap {
        position: relative;
        width: 430px;

        img {
            position: absolute;
            bottom: 0;
            left: 10px;

            &.money {
                left: 30px;
                top: 130px;
                animation: updown 3s linear infinite;

                @keyframes updown {
                    0% {
                        top: 130px;
                    }

                    50% {
                        top: 100px;
                    }

                    100% {
                        top: 130px;
                    }
                }
            }

            &:last-child {
                bottom: -30px;
                left: 0px;
            }
        }
    }

    .main_sec3_img_wrap {
        position: relative;
        width: 515px;

        img {
            position: absolute;

            &:nth-child(1) {
                right: -40px;
                bottom: 220px;
            }

            &:nth-child(2) {
                right: -20px;
                top: -100px;
            }

            &:nth-child(3) {
                right: -60px;
                top: -50px;
            }

            &:last-child {
                width: 515px;
                bottom: 0;
            }
        }
    }


}